* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
}

:root {
  --dark-color: #333;
  --light-dark: #666;
  --primary-color: rgb(36, 159, 168);
  --pink-color: rgb(224, 86, 109);
  --main-tansition: all 0.3s ease;
}

/* base styles */
body {
  line-height: 1.6;
  /* color: var(--dark-color); */
  background: #eee;
  font-size: 1rem;
}

p {
  margin: 0.3rem 0;
}

h1 {
  margin-bottom: 0.5rem;
  line-height: 1.6;
}

h1,
h2,
h3,
h5,
h6 {
  color: var(--dark-color);
}

p,
small strike {
  color: var(--light-dark);
}

img {
  width: 100%;
}
ul {
  list-style: none;
}

a {
  text-decoration: none;
  cursor: pointer;
}

a:hover {
  color: var(--primary-color);
}

i {
  cursor: pointer;
}

a,
input,
input::placeholder {
  color: var(--dark-color);
}

input:focus {
  outline: none;
}

input:focus .input::placeholder {
  color: var(--primary-color);
}

button {
  display: inline-block;
  border: none;
}

button:focus {
  outline: none;
}

button:hover {
  cursor: pointer;
}

/* utilities */
.container {
  padding: 0.3rem 2rem;
}

.body-container {
  padding: 1.8rem;
}

.wrapper {
  padding: 1.5rem;
}

hr {
  border: 1px solid #ddd;
  margin-bottom: 1.5rem;
}

.p-1 {
  padding: 1rem;
}
.p-2 {
  padding: 2rem;
}
.p-3 {
  padding: 3rem;
}
.py-1 {
  padding: 1rem 0;
}
.py-2 {
  padding: 2rem 0;
}
.py-3 {
  padding: 3rem 0;
}

.br-4 {
  border-radius: 6px;
}

.bg-white {
  background-color: #fff;
}

.grid-column {
  display: grid;
  grid-auto-flow: column;
}

.round-icon {
  width: 3rem;
  height: 3rem;
  color: white;
  border-radius: 50%;
  font-size: large;
  cursor: pointer;
}

.card-footer {
  position: relative;
  text-transform: uppercase;
  display: inline-block;
  z-index: 2;
  transition: all 0.3s ease;
  cursor: pointer;
}

i:hover {
  cursor: pointer;
}

.card-footer button {
  color: #fff;
  width: 2.5rem;
  height: 2.5rem;
  background: var(--primary-color);
  border-radius: 50%;
}
.card-footer:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 2.5rem;
  height: 100%;
  background: var(--primary-color);
  border-radius: 1000px;
  transition: all 0.15s linear;
  z-index: -1;
}

.card-footer h5 {
  padding: 0 1.5rem 0 0.3rem;
  display: inline;
}
.card-footer:hover:before {
  width: 100%;
}

.card-footer:hover i {
  transition:var(--main-tansition)
}

.card-footer:hover i {
  transform: translateX(10px);
}
.card-footer:hover h5 {
  color: #fff;
}

.card-footer i:hover {
  color: #fff !important;
}

.logo {
  color: #fff;
  font-weight: 600;
  font-size: 1.5rem;
}

.logo span {
  color: rgb(106, 192, 84);
  font-weight: 300;
}

#header .row-1 {
  background: #222;
}

#header .row-1 .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: rgb(106, 192, 84);
}
#header .row-1 button {
  padding: 0.5rem 1rem;
  color: #fff;
  background: #65a52a;
  border-radius: 6px;
  border: 1px dotted #65a52a;
}

#header .row-2 p {
  color: #fff;
}
#header .row-2 {
  background: var(--primary-color);
}

#header .row-2 .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#header .row-2 .container ul {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: max-content;
  gap: 2rem;
}

#header .row-2 .container ul li a {
  color: #fff;
}

#header .row-2 .container ul li i:hover {
  color: rgb(224, 86, 109) !important;
}

/* Main app */
.main-app {
  transition: all 0.6s ease;
}

.transform-main-app {
  transform: translateX(330px);
}
/* Sidebar */

#side-nav .content {
  position: fixed;
  background: #fff;
  width: 320px;
  height: 100vh;
  z-index: 300;
  transform: translateX(-320px);
  transition: var(--main-tansition);
  z-index: 2;
}

#side-nav .content.side-nav-transform {
  transform: translateX(0);
}

#side-nav .overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.2);
  z-index: 1;
}

#side-nav .content button {
  width: 100%;
  text-align: right;
  font-weight: 900;
  font-size: 1rem;
}
#side-nav .content ul li {
  border-bottom: 1px solid #eee;
  font-size: 1rem;
}

/* Navbar */
#main-nav {
  background: #fff;
}

#main-nav .container {
  display: grid;
  grid-template-columns: 1fr 5fr;
  align-items: center;
  padding: 1rem 2rem;
}

#main-nav .container .col-1 {
  width: 180px;
}
#main-nav .container .hamburger-icon {
  display: none;
  background: transparent;
  font-size: 1rem;
}

#main-nav .container .col-2 {
  display: grid;
  align-items: center;
  gap: 1rem;
  grid-template-columns: 2fr 1fr;
}

#main-nav .container .col-2 input {
  padding: 0.9rem 1rem;
  width: 100%;
  border-radius: 30px;
  border: #ddd solid 0.5px;
  font-size: 1rem;
  position: relative;
  transition: all 0.3s linear;
}

#main-nav .container .col-2 > :first-child {
  position: relative;
}

#main-nav .container .col-2 .search-icon {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  top: 2px;
  right: 2px;
  bottom: 2px;
  width: 5rem;
  background: var(--primary-color);
  border-radius: 30px;
}

#main-nav .container .col-2 > :nth-child(2),
#main-nav .container .col-2 ul {
  display: flex;
  align-items: center;
}

#main-nav .container ul {
  gap: 2rem;
  justify-self: start;
  font-weight: bold;
}

#main-nav .container .col-2 > :nth-child(2) {
  gap: 1.5rem;
  justify-self: end;
}

#main-nav .container .col-2 > :nth-child(2) h4 {
  text-transform: uppercase;
  font-weight: bold;
  display: inline;
}

#main-nav .container ul .active-nav {
  color: var(--primary-color);
}
#main-nav .container .col-2 > :nth-child(2) .cart-icon .cart-dd.empty i {
  font-size: 6rem;
  color: var(--pink-color);
}

#main-nav .container .col-2 > :nth-child(2) .cart-icon .cart-dd.empty,
#main-nav .container .col-2 > :nth-child(2) .cart-icon:hover .cart-dd.empty {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#main-nav .container .col-2 > :nth-child(2) .cart-icon {
  position: relative;
  cursor: pointer;
}
#main-nav .container .col-2 > :nth-child(2) .cart-icon .cart-dd {
  position: absolute;
  z-index: -1;
  opacity: 0;
  width: 330px;
  background: #fff;
  border-top: 4px solid var(--primary-color);
  border-radius: 15px;
  box-shadow: 0px 6px 15px 0px rgb(202, 200, 200);
  padding: 1rem;
  top: 150%;
  right: 0;
  transition: all 0.3s linear;
}
#main-nav .container .col-2 > :nth-child(2) .cart-icon .arrow {
  z-index: -1;
  opacity: 0;
  position: absolute;
  top: 115%;
  left: 30%;
  width: 0;
  height: 0;
  border-right: 10px solid transparent;
  border-left: 10px solid transparent;
  border-bottom: 10px solid var(--primary-color);
  transition: var(--main-tansition);
}

#main-nav .container .col-2 > :nth-child(2) .cart-icon:hover .cart-dd,
#main-nav .container .col-2 > :nth-child(2) .cart-icon:hover .arrow {
  display: block;
  opacity: 1;
  z-index: 1;
}
#main-nav .container .col-2 > :nth-child(2) .cart-icon .fa-cart-plus {
  position: relative;
}

#main-nav .container .col-2 > :nth-child(2) .cart-icon .cart-dd .card {
  display: grid;
  position: relative;
  grid-template-columns: 1fr 3fr 0.5fr;
  padding: 1rem 0;
  align-items: center;
  justify-items: center;
}

#main-nav .container .col-2 > :nth-child(2) .cart-icon .cart-dd .card:after {
  content: "";
  position: absolute;
  height: 1px;
  top: 100%;
  left: -1rem;
  width: calc(100% + 2rem);
  background: #ddd;
}
#main-nav
  .container
  .col-2
  > :nth-child(2)
  .cart-icon
  .cart-dd
  > .card:nth-child(2)::before {
  content: "";
  position: absolute;
  height: 1px;
  top: 0;
  left: -1rem;
  width: calc(100% + 2rem);
  background: #ddd;
}

#main-nav .container .col-2 > :nth-child(2) .cart-icon .cart-dd .head {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 1rem;
  text-align: right;
  margin-bottom: 2rem;
}

#main-nav .container .col-2 > :nth-child(2) .cart-icon .cart-dd .head span {
  color: var(--light-dark);
}

#main-nav .container .col-2 > :nth-child(2) .cart-icon .cart-dd .card img {
  width: 50px;
  grid-row: 1 / span 2;
  justify-self: start;
}

#main-nav .container .col-2 > :nth-child(2) .cart-icon .cart-dd .card button {
  grid-column: 3;
  grid-row: 1 / span 2;
  justify-self: end;
  font-weight: bold;
  background: transparent;
}

#main-nav .container .col-2 > :nth-child(2) .cart-icon .cart-dd .price {
  color: var(--pink-color);
}

#main-nav .container .col-2 > :nth-child(2) .cart-icon .cart-dd .card h5,
#main-nav
  .container
  .col-2
  > :nth-child(2)
  .cart-icon
  .cart-dd
  .card
  .price-x-quantity {
  justify-self: start;
}

#main-nav .container .col-2 > :nth-child(2) .cart-icon .cart-dd footer {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin-top: 0.9rem;
}

#main-nav .container .col-2 > :nth-child(2) .cart-icon .cart-dd footer button {
  padding: 0.8rem 2rem;
  border-radius: 200px;
  color: #fff;
  font-weight: bold;
  text-transform: uppercase;
}

#main-nav .container .col-2 > :nth-child(2) .cart-icon .cart-dd footer .btn-1 {
  background: var(--pink-color);
}

#main-nav .container .col-2 > :nth-child(2) .cart-icon .cart-dd footer .btn-2 {
  background: var(--primary-color);
}

.fa-cart-plus .count {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.6rem;
  font-weight: 900;
  background: var(--pink-color);
  border-radius: 50%;
  color: #fff;
  width: 1rem;
  height: 1rem;
  text-align: center;
  right: -25%;
  top: -25%;
}

#main-nav .container .col-2 > :nth-child(2) i {
  font-size: 1.5rem;
}

/*  Categories*/

#categories {
  display: grid;
  grid-auto-flow: column;
  gap: 1rem;
}

#categories > .card {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 0;
  width: 100%;
}

#categories > .card:hover h4 {
  color: var(--primary-color);
  transition: var(--main-tansition);
  cursor: pointer;
}

#categories > .card .round-icon {
  transition: var(--main-tansition);
  cursor: pointer;
}
#categories > .card:hover .round-icon {
  background: var(--primary-color) !important;
}

#categories > .card .divider {
  height: 60%;
  width: 1px;
  background: #999;
  opacity: 0.4;
}

#categories > .card:first-child .divider {
  display: none;
}

/* hero */

#hero .content {
  display: grid;
  gap: 1.5rem;
}

#hero .lg-ad .content {
  color: #fff;

  background: var(--primary-color);
  padding: 1rem 5rem;
  height: 80vh;
  border-radius: 6px;
  display: grid;
  grid-template-columns: 1.6fr 1fr;
  gap: 2rem;
  align-items: center;
}

#hero .lg-ad .content img {
  width: 100%;
  justify-self: end;
}

#hero .lg-ad .content .col-2 h2 {
  color: #fff;
}
#hero .lg-ad .content .col-2 {
  display: grid;
  gap: 1.5rem;
}

#hero .lg-ad .content .col-2 .benefits {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: start;
  gap: 1.5rem;
}

#hero .lg-ad .content .col-2 .benefits > :nth-child(4) {
  grid-column: 2;
  grid-row: 1 / span 3;
}

#hero .lg-ad .content .round-icon {
  color: var(--primary-color);
}

#hero .lg-ad .content .col-2 i {
  font-size: 1rem;
}

#hero .lg-ad .content .col-2 h2 {
  font-size: 4vw;
  line-height: 1;
}

#hero .lg-ad .content .col-2 .benefits > * {
  display: flex;
  align-items: center;
  gap: 1rem;
  line-height: 1.4;
}

/* hero Navigate */

#hero .pd-nav {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}

#hero .pd-nav > .card h2,
#hero .pd-nav > .card h3,
#hero .pd-nav > .card h4,
#hero .pd-nav > .card h5 {
  color: #fff;
}
#hero .pd-nav > .card {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  padding: 1.5rem;
  gap: 1rem;
  border-radius: 6px;
  background: var(--primary-color);
  color: #fff;
  position: relative;
  transition: var(--main-tansition);
  position: relative;
  /* z-index: -1; */
}

#hero .pd-nav > .card h2{
font-size:2.5rem;
line-height: 1.4
}
#hero .pd-nav > .card h3{
font-size: 2rem;
line-height: 1.4

}

#hero .pd-nav > .card a {
  color: #fff;
}

#hero .pd-nav > .card span {
  font-weight: 500;
}

#hero .pd-nav > .card h4 {
  vertical-align: middle;
  color: #fff;
}

#hero .pd-nav .card .shop-now {
  display: flex;
  align-items: center;
  gap: 1rem;
  position: absolute;
  top: 50%;
  color: var(--primary-color);
  cursor: pointer;
}
#hero .pd-nav .card .shop-now .round-icon {
  color: var(--dark-color);
  font-size: 1rem;
  transition: var(--main-tansition);
}

#hero .pd-nav .card .shop-now:hover .round-icon {
  color: var(--primary-color);
  transform: translateX(-10px);
}
#hero .pd-nav .card:nth-child(1) {
  grid-row: 1 / span 2;
  background: url("http://demo2.themelexus.com/medilazar/wp-content/uploads/2020/10/h1-banner01.jpg")
    no-repeat center center/100% 100%;
}



#hero .pd-nav .card:nth-child(2) .shop-now {
  left: 60%;
  padding-top: 1rem;
}
#hero .pd-nav .card:nth-child(2) {
  background: url("http://demo2.themelexus.com/medilazar/wp-content/uploads/2020/10/h1-banner02.jpg")
    no-repeat center center/100% 100%;
  grid-template-columns: 3fr 1fr;
}

#hero .pd-nav .card:nth-child(2) .col-1 {
  grid-column: 2;
  
}
#hero .pd-nav .card:nth-child(3) {
  background: url("http://demo2.themelexus.com/medilazar/wp-content/uploads/2020/10/h1-banner03.jpg")
    no-repeat center center/100% 100%;
}

#hero .pd-nav .card:nth-child(4) {
  grid-row: 1 / span 2;
  grid-column: 3;
  background: url("http://demo2.themelexus.com/medilazar/wp-content/uploads/2020/10/h1-banner04.jpg")
    no-repeat center center/100% 100%;
}

#hero .pd-nav .card:hover {
  background-size: 105% 105%;
}

