/* Hero Nav */
#hero-bottom-nav .divider {
  display: none;
}
#hero-bottom-nav {
  padding: 3rem 0;
}

/* DailyDeal */
#daily-deal,
#health-products {
  background: #fff;
  border-radius: 6px;
}

.grid-products .header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2rem;
}

.grid-products .header button {
  margin-left: 1.5rem;
  font-size: 1rem;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  background: #fff;
  cursor: pointer;
}

.grid-products .header button:hover {
  background: var(--primary-color);
  color: #fff;
}
.grid-products .header i:hover {
  color: #fff;
}

.grid-products .header button:disabled {
  cursor: none;
  background: none;
  color: #dddd;
}
.grid-products .header button:disabled i {
  color: #dddd;
  cursor: none;
}

.grid-products .content {
  display: grid;
  gap: 1rem;
}

.grid-products .content .card {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
  border-radius: 6px;
  background: #fff;
  box-shadow: 0.5px 0.5px 2px 2px #fff;
  transition: all 0.3s ease-in;
  z-index: 0;
}

.grid-products .content .card * {
  z-index: ;
}

.grid-products .content .card img {
  transition: all 0.3s ease-in;
}

.grid-products .content .card small,
.grid-products .content .card h3 > strike {
  color: var(--light-dark);
}
.grid-products .content .card small:hover {
  color: var(--primary-color);
}

.grid-products .content .card:hover {
  box-shadow: 1px 1px 10px 5px #ddd;
}

.grid-products .content .card:hover img {
  transform: scale(1.1);
}
.grid-products .content .card .new-price {
  color: rgb(238, 96, 120);
}

.grid-products .content .card strike {
  color: var(--dark-color);
  font-weight: 400;
}

/* Featured Brands */

#featured-brands .grid {
  display: grid;
  grid-auto-flow: column;
  gap: 2rem;
}

#featured-brands .grid .card {
  position: relative;
  display: grid;
  grid-template-rows: 3fr 1fr;
  border-radius: 6px;
}
#featured-brands .grid .card .brands {
  display: flex;
  justify-content: center;
  align-items: center;
  /* height: 10rem; */
  border-radius: 6px;
  transition: var(--main-tansition);
}

#featured-brands .grid .card .company {
  height: 6rem;
  background: coral;
  position: relative;
  margin-top: -45px;
  margin-left: 15px;
  margin-right: 15px;

  border-radius: 6px;
}

