  transform: scale(1.02);
}

/* Popular categories */

#popular-categories {
  background-color: #fff;
  text-align: center;
}

#popular-categories img {
  width: 120px;
  /* height:150px; */
}

#popular-categories .grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  /* gap:2rem */
}

/* From Our Blog */
#from-our-blog .content {
  display: grid;
  grid-template-columns: repeat(4, 1fr) !important;
}
#from-our-blog .content .card:hover {
  box-shadow: none;
}

#from-our-blog .content .card:hover img {
  transform: none;
}

#from-our-blog .content .card img {
  object-fit: contain;
}

/* Section Nav */
.section-nav {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.section-nav h3 {
  font-weight: 500;
}

.section-nav .link {
  color: #888;
  font-weight: 500;
  margin-right: 0.5rem;
}
.section-nav .link:hover {
  color: var(--primary-color);
}

/* Shop */
#shop .content-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}

#shop .shop-list .sd-search .content::-webkit-scrollbar{
  width: 3px;
}

#shop .shop-list .sd-search .content::-webkit-scrollbar-track{
  background: silver;
}

#shop .shop-list .sd-search .content::-webkit-scrollbar-thumb{
background: black;
}
#shop .shop-list .sd-search .content {
  position: fixed;
  top: 0;
  left: 0;
  width: 320px;
  background: #fff;
  height: 100vh;
  transform: translateX(-380px);
  transition: var(--main-tansition);
  padding: 0 2.5rem;
  overflow-y: scroll;
}
#shop .shop-list .sd-search .content.transform {
  transform: translateX(0);
}

#shop .shop-list .sd-search > .overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.3);
  z-index: 2;
}

#shop .shop-list .sd-search .content span {
  padding: 1rem 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: .5rem;
  background: transparent;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 900;

}

#shop .shop-list .sd-search .content .card {
 padding: 0 1.5rem;
}

#shop .shop-list .sd-search .content .card .title {
  position:relative;
  padding: 4rem 0 1rem 0;
}

#shop .shop-list .sd-search .content .card .title::before,
#shop .shop-list .sd-search .content .card .title::after{

content:'';
position: absolute;
top:0;
height: 1px;
background: rgb(226, 226, 226);
left: -1.5rem;
right:-1.5rem
}

#shop .shop-list .sd-search .content .card .title::after{
  top: 100%;
}

#shop .shop-list .sd-search .content .card  ul{
  padding: 1.5rem 0;
}
#shop .shop-list .sd-search .content .card  ul li{
  padding:.5rem 0;
  font-size: 1rem;
  display: flex;
  align-items: center;
  gap: .5rem;
}

#shop .shop-list .sd-search .content .card  ul li i{
  font-size: .6rem;
}

#shop .shop-list .sorting-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#shop .shop-list .sorting-wrapper div {
  display: flex;
  align-items: center;
  gap: 1rem;
}

#shop .shop-list .sorting-wrapper div select {
  padding: 0.3rem 0.5rem;
  border-radius: 200px;
  width: 100%;
}

select:focus {
  outline: none;
  border: 1px solid #333;
}

#shop .shop-list .content {
  gap: 1.5rem;
}

