} */

#shop .shop-list .grid-products .content .card .card-footer span {
  padding: 0 1rem;
}

/* Paginate */

.paginate {
  display: flex;
  gap: 1rem;
  margin-top: 2rem;
}

.paginate button {
  font-size: 0.9rem;
  font-weight: 600;
}

.paginate button:hover {
  color: var(--primary-color);
}

.paginate .round-icon {
  background: transparent;
  color: var(--primary-color);
  width: 2rem;
  height: 2rem;
}

.paginate .round-icon:hover {
  background: var(--primary-color);
  color: #fff;
}
.paginate .round-icon.active {
  background: var(--primary-color);
  color: #fff;
}

