/* Shop Search */

#shop .shop-search article {
  background: #fff;
  border-radius: 6px;
  padding: 1rem 1.5rem 1rem 1.5rem;
}
#shop .shop-search article .title {
  padding-bottom: 0.55rem;
  position: relative;
  margin-bottom: 1.5rem;
}

#shop .shop-search article h2:before {
  content: "";
  position: absolute;
  top: calc(100%);
  left: -1.5rem;
  height: 1px;
  width: calc(100% + 3rem);
  background: #ddd;
}

#shop .shop-search article .content {
  /* padding: 0 1.5rem; */
}

#shop .shop-search span {
  display: inline-block;
  transition: var(--main-tansition);
  color: var(--dark-color);
}

#shop .shop-search .link {
  margin-bottom: 1rem;
  gap: 0.5rem;
  display: flex;
  align-items: center;
}

#shop .shop-search i {
  font-size: 0.7rem;
}

#shop .shop-search .active-transform {
  transform: translateX(10px);
}
#shop .shop-search .active-link {
  color: var(--primary-color);
}

/* Bottom-Nav */

#bottom-nav{
  display: none;
  grid-template-columns: repeat(3,1fr);
  justify-items: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 3;
}

#bottom-nav > button{
  padding: 1rem 2rem;
  font-size: 1.5rem;
  color: #fff;
  background: #444;
  border:1px solid var(--light-dark) ;
  width: 100%;
  
}

#bottom-nav > button .fa-cart-plus{
  position: relative;
}

#bottom-nav > button .fa-cart-plus .count{
  background: var(--primary-color);
  width: 1.5rem;
  height: 1.5rem;
  font-size: .75rem;
  top:-40%;
  right:-50%;

  
}