@media (max-width: 1320px) {
  #shop .shop-search {
    display: none;
  }
  #shop .content-grid {
    grid-template-columns: unset;
  }

  #shop {
    max-width: 1050px;
    margin: auto;
  }
}

@media (max-width: 1200px) {

  body {
    font-size: 0.9rem;
    overflow-x: hidden;
  }

  #main-nav .container .col-2 {
    display: none;
  }

  #main-nav .container {
    display: flex;
    justify-content: space-between;
    grid-template-columns: 1fr;
  }

  #main-nav .container .hamburger-icon {
    display: block;
  }

  #categories {
    display: grid;
    grid-auto-flow: unset;
    grid-auto-columns: unset;
    grid-template-columns: 1fr 1fr 1fr;
    gap: unset;
  }

  #categories > .card .divider {
    display: none;
  }

  #categories > .card {
    padding: 0.8rem 0;
    margin-right: 3rem;
  }

  #categories > .card:nth-child(2),
  #categories > .card:nth-child(3),
  #categories > .card:nth-child(5) {
    padding-left: 1.5rem;
  }

 
  #hero .pd-nav {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }

  #hero .pd-nav .card:nth-child(1) {
    grid-row: 1 / span 2;
    grid-column: 1;
  }

  #hero .pd-nav .card:nth-child(4) {
    grid-row: 3 / span 4;
    /* height: 22rem; */
    grid-column: 1 / span 2;
  }

  #hero .lg-ad .content {
    height: 22rem;
    grid-template-columns: 1fr 1fr;
    padding: 1rem 2rem;
  }

  .grid-products .content,
  #featured-brands .grid{
    display: grid;
    grid-auto-flow: unset;
    grid-auto-columns: unset;
    grid-template-columns: repeat(3, 1fr) !important;
  }

  #popular-categories .grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }

  #from-our-blog .content {
    display: grid;
    grid-template-columns: repeat(2, 1fr) !important;
  }

  #bottom-nav {
    display: grid;
  }
}



@media (max-width: 1100px) {
  #shop {
    max-width: 700px;
  }

  #shop .shop-list .grid-products .content {
    grid-template-columns: 1fr 1fr !important;
  }
}


@media(max-width:850px){
  
  #hero .lg-ad .content .col-2 .benefits > :nth-child(4),
  #hero .lg-ad .content .col-2 .benefits > :nth-child(1){
    display: none;
  }

  #hero .lg-ad .content .col-2 .benefits {
    display: grid;
    grid-template-columns: 1fr;
    align-items: start;
    gap: 2.5rem;
  }

  #hero .lg-ad .content {
    grid-template-columns: 1.5fr 1fr;
  }
}

@media (max-width: 760px) {

  #shop {
    max-width: 600px;
    margin: auto;
  }
  .container {
    padding: 0.3rem 1rem;
  }
  #hero > #categories {
    display: none;
  }

  #hero-bottom-nav {
    padding: 1.5rem 0;
  }

  

  
  #hero-bottom-nav #categories,
  .grid-products .content, 
  #from-our-blog .content 
   {
    grid-template-columns: unset !important;
    justify-content: center;
  }

  #categories > .card:nth-child(2),
  #categories > .card:nth-child(3),
  #categories > .card:nth-child(5) {
    padding-left: unset;
  }

  #hero-bottom-nav #categories .card,
  #hero-bottom-nav #categories .card .col-2 {
    display: flex;
    flex-direction: column;
    text-align: center;
  }

  #hero .pd-nav {
    display: grid;
    grid-template-columns:unset;
    gap: 2rem;
  }

  #hero .pd-nav .card:nth-child(1),
  #hero .pd-nav .card:nth-child(2),
  #hero .pd-nav .card:nth-child(3),
  #hero .pd-nav .card:nth-child(4)
   {
    grid-row: unset;
    grid-column: unset;
  }

  #hero .pd-nav > .card h2{
    font-size:2rem;
    }
    

    #hero .pd-nav  .card:before{
      content: '';
      position: absolute;
      bottom:0;
      left: 0;
      right: 0;
      top: 0;
      background: rgba(0,0,0,0.4);
      border-radius: 4px;
    }

    #hero .pd-nav .card *{
      z-index: 1;
    }

    #hero .pd-nav .card:nth-child(2) .shop-now {
      left: 1.5rem;
    }

    #hero .pd-nav .card:nth-child(2) .col-1{
      grid-column: 1;
    }

    #hero .pd-nav .card{
      background-size: 100%  !important;
      transition: all .3s ease !important;
    }

    #hero .pd-nav .card:hover{
      background-size:110% !important
    }

    #hero .pd-nav .card:nth-child(4) {
      grid-template-columns: unset;
    }

    #popular-categories .grid {
      grid-template-columns: 1fr 1fr;
    }

    #featured-brands .grid{
      grid-template-columns: 1fr!important
    }
}
